.intro {
  padding: 12%;
  height: calc(#{$headerIntroHeight} - 54px);
  box-sizing: border-box;
  background-image: url(../images/slider/slide-img-1.jpg);
  background-color: $bgColor1;
  background-size: cover;
  z-index: 1;

  &__heading {
    width: 40%;
    font-size: $subtitleFont;
    color: $textColor3;
    font-weight: 800;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__text {
    width: 60%;
    margin-top: 1em;
    font-size: $mainFont;
    font-weight: 800;
    line-height: 1.5;
    color: $textColor1;
  }
}

@media (min-width: 768px) {
  .intro {
    padding: 0;
    position: absolute;
    left: $sliderButtonSizeBig * 4;
    top: #{-$headerIntroHeightBig / 2};
    height: $headerIntroHeightBig;
    width: calc(100% - #{$sliderButtonSizeBig * 9 - 30px});

    &__heading {
      font-size: $mainFontBig;
      position: absolute;
      bottom: 70%;
      left: 98px;
    }

    &__text {
      font-size: $subtitleFontBig;
      position: absolute;
      bottom: 30%;
      left: 98px;
      width: 220px;
    }
  }
}

@media (min-width: 1024px) {
  .intro {
    left: $sliderButtonSizeBig * 9;
    width: calc(100% - #{$sliderButtonSizeBig * 19 - 30px});
  }
}

@media (min-width: 1440px) {
  .intro {
    position: absolute;
    left: calc(50% - 573px);
    top: #{-$headerIntroHeightBiggest / 2};
    height: $headerIntroHeightBiggest;
    width: 1146px;

    &__heading {
      font-size: $mainAddFontBiggest;
      position: absolute;
      bottom: 144px;
      left: 98px;
    }

    &__text {
      font-size: $subtitleAddFondBiggest;
      line-height: 1.4;
      position: absolute;
      bottom: 85px;
      left: 98px;
      width: 264px;
    }
  }
}
