.testimonials {
  padding-top: 6em;
  text-align: center;

  &__heading-container {
    @include flex-column-center;
  }

  &__subheading {
    margin-top: 0.5em;
  }

  &__card-container {
    margin-top: 4em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__card {
    margin-top: 4em;
  }
}

@media (min-width: 768px) {
  .testimonials {
    text-align: start;

    &__heading-container {
      width: 362px;
      @include flex-column-center;
      align-items: flex-start;
    }

    &__card-container {
      justify-content: space-between;
    }
    
    &__card {
      &:last-of-type {
        margin: 60px auto 0;
      }
    }
  }
}

@media (min-width: 1440px) {
  .testimonials {
    padding-top: 165px;

    &__subheading {
      margin-top: 19px;
    }
    
    &__card-container {
      margin-top: 86px;
    }

    &__card {
      margin: 0;

      &:last-of-type {
        margin: 0;
      }
    }
  }
}
